:root {
  --grey: #312f2f;
}

.navbar {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: space-between;
  /* justify-content: space-around; */
  width: 100%;
  background-color: rgb(203 203 203);
  height: 80px;
  transition: 0.2s all ease-in;
  z-index: 1;
}

.navbar .toggle {
  display: flex;
  width: 1300px;
  justify-content: inherit;
}

.navbar ul {
  list-style: none;
  display: flex;
  align-items: center;
}

.navbar ul a {
  font-size: 1.25em;
  text-transform: capitalize;
  margin-left: 85px;
  border: 2px solid transparent;
  font-weight: 100;
}

.navbar a {
  text-decoration: none;
  color: var(--grey);
}

.navbar:hover {
  opacity: 1;
}

.brand-name {
  display: flex;
  background: rgb(212, 138, 76);
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  height: 100%;
  align-items: center;
}

.navbar a:hover {
  border-bottom: 2px solid rgb(212, 138, 76);
}

.simpleShop {
  margin-left: 150px;
  color: rgb(70 68 68);

  font-size: 35px;

  padding-left: 50px;
  padding-right: 20px;
  cursor: pointer;
  font-weight: 100;
  padding-bottom: 6px;
  transition: all 0.5s ease;
}

.simpleShop:hover {
  color: rgb(0, 0, 0);
}

.user-section {
  display: flex;
  align-items: center;
  width: 410px;
  justify-content: flex-end;
  padding-right: 40px;
}

#cartWidget {
  border: none;
  padding-right: 24px;
  padding-bottom: 5px;
}

#profile-link {
  border: 1px solid transparent;
}

#profile-link .user-profile-link {
  width: 35px;
  border-radius: 50%;
}

.active {
  opacity: 0.5;
}
.hamburger-toggle {
  font-size: 1.5rem;
  background: transparent;
  border-color: transparent;
  cursor: pointer;
  transition: all 0.3s linear;
  margin-right: 20px;
  display: none;
}

.hamburger-toggle:hover {
  color: grey;
}

@media (max-width: 480px) {
  .brand-name .simpleShop {
    padding-left: 20px;
    margin-left: 0px;
  }

  .navbar .toggle {
    display: grid;
    position: absolute;
    width: 100%;
    height: 0;
    grid-template-columns: repeat(1, 1fr);
    top: 80px;
    background: white;
    justify-content: center;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
  }
  .navbar .toggle ul {
    display: grid;
    height: 300px;
    grid-gap: 65px;
    padding-top: 40px;
  }
  .navbar .toggle ul a {
    margin: auto;
    font-size: 30px;
  }
  .navbar .toggle .category {
    display: grid;
  }
  .navbar .toggle .category .subMenu {
    width: auto;
    margin-left: 0;
    position: inherit;
    background: white;
  }
  .navbar .toggle .category .subMenu .show {
    flex-direction: column;
    width: auto;
    margin: auto;
    align-items: baseline;
    width: auto;
    height: auto;
    padding: 3px 0px 3px;
    border-radius: 4px;
    opacity: 0.9;
    background: white;
  }
  .hamburger-toggle {
    display: block;
    color: var(--grey);
    position: fixed;
    left: 430px;
  }
  .user-section {
    grid-gap: 20px;
    align-items: center;
    justify-content: center;
    width: auto;
    display: grid;
    height: 330px;
  }
  .user-section {
    padding-top: 130px;
    padding-right: 0;
  }
  .user-section #cartWidget {
    padding-right: 15px;
    display: flex;
    align-self: baseline;
    justify-self: center;
  }
  .user-section #cartWidget .cartWidget-container svg {
    height: 50px;
    width: 50px;
  }
  .user-section #profile-link {
    display: flex;
    justify-self: center;
  }
  #profile-link .user-profile-link {
    width: 50px;
  }
  .user-section .signButton {
    padding-left: 0;
  }
  body {
    overflow: hidden;
  }
}
