.footer {
  position: absolute;
  display: flex;
  border-left: none;
  border-right: none;
  border-bottom: none;
  flex-direction: column;
  align-items: center;
  background: #cbcbcb;
  height: fit-content;
  width: 100%;
  margin-top: 51px;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 56%;
  padding-top: 10px;
}

.footer-container section {
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-container h4 {
  font-size: 20px;
  padding-bottom: 10px;
  letter-spacing: 1px;
  font-weight: 350;
}

.contacto h4 {
  padding-bottom: 10px;
}

.contacto .cnt {
  display: flex;
  align-items: flex-end;
  padding-bottom: 20px;
  width: 230px;
}

.contacto p {
  padding-left: 10px;
  line-height: 0.8;
  align-self: center;
}

.redes a {
  display: flex;
  color: black;
  margin-bottom: 10px;
  align-self: center;
  width: 200px;
}

.redes a:hover {
  color: rgb(212, 138, 76);
}

.redes p {
  font-size: 15px;
  padding-left: 5px;
}

.github {
  color: black;
  margin-top: -20px;
}

.github:hover {
  color: rgb(212, 138, 76);
}

.ayuda p {
  cursor: pointer;
  margin-bottom: 10px;
  border-bottom: 1px solid transparent;
}

.ayuda p:hover {
  border-bottom: 1px solid rgb(212, 138, 76);
}

@media (max-width: 480px) {
  .footer-container {
    grid-template-columns: repeat(1, 1fr);
  }
  .contacto .cnt {
    padding-bottom: 10px;
  }
  .footer-container section {
    padding-bottom: 5px;
    padding-top: 5px;
  }
  .footer-container .ayuda p {
    text-align: center;
  }
  .github {
    margin-top: -5px;
  }
}
