.cart-container {
  padding-top: 140px;
  height: 815px;
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 830px;
  align-items: center;
}

.cart-container h1 {
  color: var(--grey);
  font-size: 40px;
  border-bottom: 1px solid #d48a4c;
  font-weight: 400;
  letter-spacing: 2px;
}

.cart-container h2 {
  font-size: 30px;
  padding-top: 20px;
  font-weight: 400;
  text-align: center;
}

.cart-container-links {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  align-items: center;
}

.cart-container-links a {
  width: 250px;
  height: 60px;
}

.cart-container-links span {
  word-spacing: 2px;
  letter-spacing: 1px;
}

.cart-container .MuiButton-outlinedPrimary {
  border: 1px solid rgb(212 138 76);
  width: 250px;
  height: 60px;
}

.cart-container .MuiButton-outlinedPrimary:hover {
  border: 1px solid #d23817;
}

.total-price {
  display: flex;
  font-size: 25px;
  font-weight: 500;
  padding-top: 20px;
  align-self: flex-end;
}

.total-price p {
  border-bottom: 1px solid black;
  margin-left: 18px;
  font-size: 24px;
  margin-right: 10px;
}

.total-price svg {
  margin-left: 36px;
  align-self: center;
  cursor: pointer;
  width: 26px;
}

.final-buy {
  display: flex;
  align-self: flex-end;
  margin-right: 69px;
}

.buy-container p {
  text-align: center;
  font-size: 22px;
  margin-top: 17px;
}

.buy-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buy-container button {
  margin: auto;
  margin-top: 20px;
}

.buy-container a {
  width: 250px;
}

.cart-container .sk-listContainer {
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
  background-color: rgb(255, 253, 253);
  display: flex;
  align-self: center;
  top: 50%;
  margin: auto;
  padding-top: 40px;
  margin-top: -220px;
}

.total-items-detail {
  width: 830px;
  display: flex;
  flex-direction: column;
}

@media (max-width: 480px) {
  .cart-container {
    display: grid;
    grid-template-columns: repeat(1, 470px);
    padding-top: 100px;
    justify-content: center;
    height: 540px;
    width: auto;
  }
  .cart-container h1 {
    text-align: center;
  }
  .total-items-detail {
    display: grid;
    grid-template-columns: repeat(1, 480px);
  }
  .total-price {
    margin: auto;
  }
  .total-price svg {
    margin-left: 0;
  }
  .final-buy {
    margin: auto;
  }
}
