@media (max-width: 480px) {
  .brand-name .simpleShop {
    padding-left: 20px;
    margin-left: 0px;
  }

  .navbar .toggle {
    display: grid;
    position: absolute;
    width: 100%;
    height: 0;
    grid-template-columns: repeat(1, 1fr);
    top: 80px;
    background: white;
    justify-content: center;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
  }
  .navbar .toggle ul {
    display: grid;
    height: 300px;
    grid-gap: 65px;
    padding-top: 40px;
  }
  .navbar .toggle ul a {
    margin: auto;
    font-size: 30px;
  }
  .navbar .toggle .category {
    display: grid;
  }
  .navbar .toggle .category .subMenu {
    width: auto;
    margin-left: 0;
    position: inherit;
    background: white;
  }
  .navbar .toggle .category .subMenu .show {
    flex-direction: column;
    width: auto;
    margin: auto;
    align-items: baseline;
    width: auto;
    height: auto;
    padding: 3px 0px 3px;
    border-radius: 4px;
    opacity: 0.9;
    background: white;
  }
  .hamburger-toggle {
    display: block;
    color: #312f2f;
    color: var(--grey);
    position: fixed;
    left: 430px;
  }
  .user-section {
    grid-gap: 20px;
    align-items: center;
    justify-content: center;
    width: auto;
    display: grid;
    height: 330px;
  }
  .user-section {
    padding-top: 130px;
    padding-right: 0;
  }
  .user-section #cartWidget {
    padding-right: 15px;
    display: flex;
    align-self: baseline;
    justify-self: center;
  }
  .user-section #cartWidget .cartWidget-container svg {
    height: 50px;
    width: 50px;
  }
  .user-section #profile-link {
    display: flex;
    justify-self: center;
  }
  #profile-link .user-profile-link {
    width: 50px;
  }
  .user-section .signButton {
    padding-left: 0;
  }
  body {
    overflow: hidden;
  }
